.score-card-container {
	display: flex;
	justify-content: center;
	align-content: center;
	align-items: center;
	height: 100vh;
	background: radial-gradient(263px at 100.2% 3%, rgb(12, 85, 141) 31.1%, rgb(205, 181, 93) 36.4%, rgb(244, 102, 90) 50.9%, rgb(199, 206, 187) 60.7%, rgb(249, 140, 69) 72.5%, rgba(16, 29, 41, 0.919) 72.6%);
	width: 100%;
}

