.score-card {
	position: relative;
	height: 22rem;
	width: 22rem;
	overflow: hidden;
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	border: 1px solid white;
	-webkit-box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0.75);
	-moz-box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0.75);
	box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0.75);
}

.image {
	height: 100%;
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
}

.design {
	display: block;
	height: 4rem;
	width: 15rem;
	background-color: #fff;
	position: absolute;
	right: 0%;
	bottom: 0%;
	transform: translate(50%, 50%) rotate(45deg);
	border-radius: 0.5rem;

}

.score {
	color: #BA3F1D;
	background-color: transparent;
	text-align: center;
	font-size: 16px;
	position: absolute;
	right: 0%;
	bottom: -2%;
	line-height: 40px;
	font-family: cursive;
	font-weight: 100;
	display: flex;
	justify-content: space-evenly;
}


.score-card:hover {
	height: 23rem;
	width: 23rem;
	-webkit-box-shadow: 20px 20px 0px 0px rgba(0, 0, 0, 0.75);
	-moz-box-shadow: 20px 20px 0px 0px rgba(0, 0, 0, 0.75);
	box-shadow: 20px 20px 0px 0px rgba(0, 0, 0, 0.75);
}

.score-card:hover .image {
	transform: translate(-50%, -50%) scale(1.2);
}

.score-card:hover .design {
	transform: translate(-30%, -205%) scale(1.2) rotate(-180deg);
	background-color: rgba(255, 255, 255, 0.8);
}

.score-card:hover .score {
	transform: translate(-70px, -150px) scale(1.2) rotate(-360deg);
	font-size: 26px;
	font-weight: 800;
	width: 15rem;


}


.cont-btn {
	outline: none;
	border: none;
	text-decoration: none;
	background-color: black;
	color: white;
	box-shadow: 1px 1px 2px 2px gray;
	height: 1.7rem;
	width: 5rem;
	font-size: 1rem;
}

@media only screen and (max-width:720px) {
	.score span{
		font-size:2.5rem;
		margin-right: 30px;
	}
}

@media only screen and (max-width:480px) {
	.score span{
		font-size:2.5rem;
		margin-right: 30px;
	}

	.score-card {
		height: 19rem;
		width: 18rem;
	}

	.design {
		height: 4rem;
		width: 13rem;
	}

	.score-card:hover {
		height: 21rem;
		width: 21rem;
	}
}




